


.input--custom {
	box-shadow: none !important;
    border: none;
    border-radius: 0;
    padding: 0;
    border-bottom: 2px solid #d3d3d3;
    height: 30px;
    line-height: 30px;
    background: #FFF;
}

.txt-area-custom {
	box-shadow: none !important;
    border: none;
    border-radius: 0;
    padding: 0;
    border-bottom: 2px solid #d3d3d3;
    min-height: 80px;
    resize: none;
}
.txt-area-custom:focus {
	outline: none !important;
	box-shadow: none !important;
}
.create-group{
	width: 100%;
	*:focus {
	    outline: none !important;
	    box-shadow: none !important;
	}
}

.txt-input {
	@extend .input--custom;
}

.ui-select-container {
	
	@extend .input--custom;
	input,.ui-select-toggle {
		@extend .input--custom;
	}
	.btn {
		box-shadow: none;
		border-radius: 0;
		
		&:hover {
			background: none;
	        color: #006699;
		}
	}
	.dropdown-menu {
	    background: #fff;
	    padding: 0;
	    border-radius: 0;
	    margin-top: 2px;
	}

	.input-txt{
		border-radius: 0;
	}
} 


.ui-select-bootstrap .ui-select-toggle > .caret {
	pointer-events: none;
}
