// basic standard styling for elements
a {
  color: $link-color;
  text-decoration: none;
}

a:active, a:focus {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  color: $link-color-hover;
}

ol {
  list-style-type: decimal;
  padding-left: 3em;
}

svg { -webkit-tap-highlight-color: rgba(255, 255, 255, 0); }





/********* Taskspace Annotation Digest ***********/ 


.l-input {
	height : 30px;
	margin: 5px auto 5px auto;
	line-height: 30px;
}


.btn-numici {
	background: #069 !important;
    color: #fff;
    border: 1px solid #069 !important;
}

.btn-numici:focus {
	color: #fff !important;
}

#digest-border-setting.ui-select-bootstrap .ui-select-choices {
	background-color: rgb(221, 221, 221);
}
#digest-border-setting.ui-select-bootstrap .ui-select-choices-row.active>a {
	padding: 0px;
}
#digest-border-setting.ui-select-bootstrap .ui-select-match .btn-default {
	text-shadow: none;
	padding: 6px 12px;
}
#digest-border-setting.ui-select-bootstrap .ui-select-choices-row>span {
	padding: 3px 15px;
}
#digest-border-setting.ui-select-bootstrap .ui-select-match .ui-select-match-text {
	width: calc(100% - 10px);
	height: 15px;
    line-height: 15px;
}
#digest-annotation-style-setting.ui-select-bootstrap .ui-select-match .ui-select-match-text {
	width: calc(100% - 10px);
	text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.ui-select-match.btn-default-focus , .btn-clean:focus{
	outline: none;
    box-shadow: none;
}


.pretty {
  white-space: normal;
  .state {
    label {
      line-height: calc(1em + 4px);
      &::after, &::before {
        top: -2px !important;
      }
    }
  }
}