/* The groups dropdown list. */

$group-list-width: 270px;
$group-list-spacing-below: 50px;

.group-list {
  .dropdown {
    white-space: nowrap;
  }
  .dropdown-toggle {
  	.group-list-label__label {
  		float: left;
	    max-width: $group-list-width - 70px;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
    }
  }
  .dropdown-menu {
    width: $group-list-width;
    max-height: 500px;  // fallback for browsers lacking support for vh/calc
    max-height: calc(100vh - #{$top-bar-height} - #{$group-list-spacing-below});
    overflow-y: auto;

    .group-name {
      overflow: hidden;
      text-overflow: ellipsis;
      width: $group-list-width - 30px;
    }
  }

  .group-item {
    display: flex;
    flex-direction: row;
    flex-grow: 1;

    padding: 0px 10px;
    cursor: pointer;
    width: 100%;
    &:hover {
    	background: #eee;
      .group-name-link {
        color: $brand-color;
      }
    }

    &.selected {
    	background: #eee;
      .group-name-link {
        font-size: $body2-font-size;
        font-weight: 600;
      }
    }
   
    &.group-disabled {
      background-color: #eee;
      .group-name-link {
        cursor: not-allowed;
      }
      .group-name-container {
        cursor: not-allowed;
      }
    }
   
   [class^="h-icon-"]{
   		font-size: 20px;
   }
   
  }

  .group-icon-container {
    margin-right: 10px;
  }

  .group-menu-icon-container {
    margin-right: 10px;
    width: 15px;
    height: 15px;
  }

  .group-cancel-icon-container {
    // the 'Leave group' icon is shifted down slightly
    // so that it lines up vertically with the 'chat heads' icon on the
    // left-hand side of the groups list
    padding-top: 3px;
    margin-right: 2px;
  }

  .group-details {
    flex-grow: 1;
    flex-shrink: 1;
    width: calc(100% - 75px);
    word-break: break-all;
    
    .share-link-container {
		//display: none;
		height: 30px;
		.share-link {
			display: flex;
		    flex-direction: row;
		    flex-grow: 1;
		    align-items: center;
		    height: 100%;
		    
		    .list-icon{
		    	width: 25px;
			  	height: 100%;
			  	justify-content: center;
			  	display: flex;
			  	align-items: center;
			  	&:hover {
    				&:before {
    					color: #069 !important;
    				}
    			}
		    }
		   
		    .list-text{
		    	display: flex;
				flex-direction: row;
				flex-grow: 1;
				align-items: center;
				height: 100%;
		    }
		}
	}
  }
 
  .group-details:hover {
  	.share-link-container {
		//display: block;
	}
  }

  .new-group-btn {
    background-color: $gray-lightest;

    .group-item {
      padding-top: 12px;
      padding-bottom: 12px;
    }

    .h-icon-add {
      font-weight: bold;
    }
  }
}

// the icon indicating the type of group currently selected at
// the top of the groups list
.group-list-label__icon {
  color: $color-gray;
  display: inline-block;
  margin-right: 4px;
  position: relative;
  vertical-align: baseline;

  // align the base of the chat-heads icon for groups
  // with the baseline of the group name label
  transform: translateY(1px);
}

.group-list-label__icon--organization {
  height: 15px;
  width: 15px;
  top: 2px;
}

// the label showing the currently selected group which opens
// the drop-down list when clicked
.group-list-label__label {
  font-size: $body2-font-size;
  font-weight:bold;
}

// the name of a group in the groups drop-down list
// and 'Post to <Group>' button for saving annotations
.group-name-link {
  white-space: normal;
  color: inherit;
}


select-section {
	width: 100%;
	height: 100%;
}

.select_section {
	border: 2px solid #d3d3d3;
		
	.ui-select-choices {
		border: 2px solid #d3d3d3;
	}
	
	.ui-select-match-text {
		padding: 0px 5px;
	}
	
	input {
		padding: 0px 5px;
	}
}
