/* The groups move-web-resource list. */

$move-web-resource-modal-body-height: 300px;

.move-web-resource-modal-body {
	overflow-y: auto;
    max-height: $move-web-resource-modal-body-height;
    background: #eee;
    .move-web-resource-err-msg {
    	color: red;
    }
    .group-bar {
    	background: #fff;
    	height: 40px;
    	line-height: 40px;
    }
    .ellipsis {
    	text-overflow: ellipsis;
    	white-space: nowrap;
    	overflow: hidden;
    }
    .group-disabled {
    	background: #ddd!important;
    	color: #000!important;
    	cursor: not-allowed;
    }
    .group-selected {
    	background: #069!important;
    	color: #fff!important;
    }
}